.top-gallery {

  .swiper-container {
    height: 100%;
    max-height: 100%;
    overflow: hidden;
  }

  .swiper-wrapper {
    height: calc(100vh - 70px) !important;
    min-height: 600px;
  }

  .swiper-slide {
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .swiper-buttons {
    position: absolute;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 60px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;

    .swiper-button-prev, .swiper-button-next {
      width: 60px;
      height: 60px;
      background-color: rgba(255, 255, 255, 0.5);
      position: static;
      transform: none;
      margin: 1px;
      box-shadow: 0px 0px 4px 1px rgba($color: #000000, $alpha: 0.2);
      background-repeat: no-repeat;
      background-size: auto;
      background-position: center;
    }

    .swiper-button-prev {
      background-image: url('../../images/svg/arrow-prev.svg');
    }
    .swiper-button-next {
      background-image: url('../../images/svg/arrow-next.svg');
    }
  }

  .swiper-pagination {
    bottom: 30px;
  }

  .swiper-pagination-bullet {
    background: #857122;
    opacity: 0.5;
  }
  .swiper-pagination-bullet-active {
    background: #857122 !important;
    opacity: 1;
  }
}

.top-gallery__text {
  background: rgba(0, 0, 0, 0.8);
  padding-left: 48px;
  padding-right: 48px;
  padding-top: 24px;
  padding-bottom: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}