.modal-form__container { 
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  display: none;
  z-index: 1000;
}

.modal-form {
  width: 440px;
  padding: 40px;
  background-color: $main-color;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  @media (max-width: 640px) {
    width: 100%;
  }
   h2 {
     font-size: 22px;
     margin-bottom: 0.5rem;
     color: $mark-font-color;
   }
  p {
    font-family: $title-font;
    font-size: 1rem;
  }
}

.modal-form__close {
  width: 25px;
  height: 25px;
  border: none;
  background-color: transparent;
  background-image: url(../../images/svg/close.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}

.modal-form__input {
  font-size: 18px;
  font-family: $title-font;
  display: block;
  width: 100%;
  border: 2px solid $mark-font-color;
  padding: 12px 16px;
  margin-bottom: 1rem;
  color: $main-color;
}


.personal-info-policy {
  line-height: 1.2;
  padding-left: 32px;
  display: block;
  margin-bottom: 16px;
  position: relative;  
  font-size: 14px;
  .custom-checkbox {
    display: block;
    position: absolute;
    width: 18px;
    height: 18px;
    border: 2px solid $mark-font-color;
    background-color: #fff;
    left: 0;
    top: 0;
    background-repeat: no-repeat;
    background-position: center;
  }

  input[type="checkbox"]:checked+.custom-checkbox {
    background-image: url('../../images/svg/checked.svg');
  }
}