.product-card {
  background: #2C2C2C;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  overflow: hidden;
}

.product-card__image {
  img {
    display: block;
    width: 100%;
  }
}

.products-card__text {
  padding: 1.5rem 0.8rem;
  padding-bottom: 0;
  h4 {
    margin-bottom: 4px;
  }
  p {
    margin-bottom: 2px;
    font-weight: normal;
    span {
      color: $mark-font-color;
    }
  }
}
.products-card__btn {
  padding: 1rem 1rem 1.5rem;
  text-align: center;
}