.light {
  color: rgb(224, 218, 202);
}

.dark {
  color: #000;
}

@for $i from 1 through 10 {
  .mb-#{$i} {
    margin-bottom: 8 * $i + px;
  }
  .mt-#{$i} {
    margin-top: 8 * $i + px;
  }
  .pt-#{$i} {
    padding-top: 8 * $i + px;
  }
  .pb-#{$i} {
    padding-bottom: 8 * $i + px;
  }
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.hide-scroll {
  &::-webkit-scrollbar {
    height: 0;
    width: 0;
  }

  &::-webkit-scrollbar-thumb {
    height: 0;
    width: 0;
  }

  &::-webkit-scrollbar-track {
    height: 0;
    width: 0;
  }
}

.hide-overflow {
  overflow: hidden;
}
