@import url('https://fonts.googleapis.com/css?family=Cuprum:400,700|Open+Sans:300,400,700&subset=cyrillic');

html, :root {
  font-size: 16px;
}

body {
  font-family: $main-font;
  color: $main-font-color;
  line-height: 1.5;
  background-color: $secondary-bg-color;
}

.main-wrapper {
  padding-top: 70px;
}

h1, h2, h3, h4 {
  line-height: 1.2;
  margin-top: 0;
  font-family: $title-font;
  text-transform: uppercase;
  font-weight: normal;
  span {
    color: $mark-font-color;
  }
}

h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1rem;
}

p {
  margin-top: 0;
  font-weight: lighter;
}

a {
  color: $main-font-color;
  text-decoration: none;
  &:hover {
    @media (min-width: 1024px) {
      text-decoration: underline;
    }
    
  }
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.contacts__links {
  display: flex;
  align-items: center;
  font-family: $title-font;
  a:first-of-type {
    margin-right: 16px;
  }
}