.header {
  background-color: $main-color;
  font-family: $title-font;
  padding-top: 8px;
  padding-bottom: 8px;
  position: fixed;
  z-index: 100;
  width: 100%;
  left: 0;
  top: 0;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);

  .contacts__links {
    height: 100%;
    justify-content: flex-end;
  }
}

.header__logo-link {
  display: inline-block;
}

.header__nav {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 8px;
  padding-bottom: 8px;
  ul {
    display: flex;
    flex-wrap: wrap;
    font-size: 18px;
    li {
      flex: auto 1 auto;
      padding-left: 8px;
      padding-right: 8px;
    }
  }
}