.main-top {
  height: calc(100vh - 70px);
  min-height: 600px;
  background-color: $secondary-bg-color;
  &>.grid-x {
    min-height: 100%;
  }
}

.main-top__text-cont {
  background-color: #191919;
  .grid-container {
    margin-top: 30px;
    margin-bottom: 30px;
  }
  h1 {
    span {
      color: $mark-font-color;
    }
  }
  p {
    margin-bottom: 2rem;
  }
}



.advantages {
  padding-top: 6rem;
  padding-bottom: 2rem;
  background-color: $main-color;
  h2 {
    margin-bottom: 4rem;
  }
}
.advantages__item  {
  margin-bottom: 4rem;
  * {
    text-align: center;
  }
  .advantages__item-img {
    margin-bottom: 2rem;
  }
  h3 {
    margin-bottom: 0.5rem;
  }
}


.popular {
  background-color: $main-color;
  padding-top: 2rem;
  padding-bottom: 8rem;
  h2 {
    margin-bottom: 4rem;
  }
}